.himg{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.himg>li{
    width: 25%;
}
.himg>li>img{
    width: 100%;
}



.hsort{
    width: 100%;
    height: 36px;
}
.hsort>img{
    width: 100%;
    height: 100%;
}

.hul{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.hul>li{
    width: 25%;
}
.hul>li>img{
    width: 100%;
}

.hk{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 50px;
}
.hk>li{
    width: 20%;
}
.hk>li>img{
    width: 100%;
}